import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';
import './footer.scss';

class Footer extends React.Component {
	render = () => {
		const {
			copyright,
			footerSlogan,
			// footerLogo,
			contact,
		} = this.props;
		return (
			<div className="footer">
				<div className="footerBox">
					<span className="copyRight">{copyright}</span>
					<span className="contact"><Link to="/contact_us">{contact}</Link></span>
					<div className="footerLogo fr">
						<img src={window.appConfig.logo} alt="" />
					</div>
					<span className="footerSlogan fr">{footerSlogan}</span>
				</div>
			</div>
		);
	};
}

Footer.propTypes = {
	copyright		: PropTypes.string,
	privacy         : PropTypes.string,
	footerSlogan	: PropTypes.string,
	contact         : PropTypes.string,
};

Footer.defaultProps = {
	copyright		: window.paramsString.footer.copyright,
	privacy         : window.paramsString.footer.privacy,
	contact         : window.paramsString.footer.contact,
	footerSlogan	: window.paramsString.footer.footerSlogan,
};

export default Footer;
