import React from 'react';
import PropTypes from 'prop-types';
import './homeBanner.scss';

class HomeBanner extends React.Component {
	render = () => {
		let { homeBanner = {
			title: "",
			content: "",
			showButton: false
		}} = this.props;
		return (<div id="banner" className="banner">
					<p className="bannerTitle">{homeBanner.title}</p>
					<p className="bannerContent">{homeBanner.content}</p>
					{ homeBanner.showButton && (
						<button className="bannerButton"><div className="playButton"></div>How it works</button>
					)}
				</div>)
		};
}

HomeBanner.propTypes = {
	homeBanner : PropTypes.exact({
		title: PropTypes.string,
		content: PropTypes.string,
		showButton : PropTypes.bool
	}),
};

HomeBanner.defaultProps = {
	homeBanner : {
		title: window.paramsString.homeBanner.title,
		content: window.paramsString.homeBanner.content,
		showButton: window.paramsString.homeBanner.showButton,
	}
}

export default HomeBanner;
