import React from 'react';
import PropTypes from 'prop-types';
// import { Link } from 'react-router-dom';
import './partnerLink.scss';

class PartnerLink extends React.Component {
	render = () => {
		const { 
			data,
			partner,
		 } = this.props;
		return (
			<div className="partnerLink">
				<p className="partner">{partner}</p>
				<ul className="partnerList">
					{data.map((item, index) => {
						return (
							<li key={index}>
								<img src={item.partnerLogo} alt="" />
							</li>
						);
					})}
				</ul>
			</div>
		);
	};
}

PartnerLink.propTypes = {
	data: PropTypes.array,
	partner: PropTypes.string
};

PartnerLink.defaultProps = {
	data: window.paramsString.partnerLink,
	partner: window.paramsString.partner,
};

export default PartnerLink;
