import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch, withRouter } from 'react-router-dom'; 

import Home from './pages/home'
import ContactUs from './pages/contactUs';
import AppShell from './utils/AppShell';

const mobileMinWidth = 1024;

class App extends Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
    this.state = {
      mobile: true,
    };
  }

  componentDidMount() {
    this.isMobile();
    window.addEventListener('resize', this.isMobile);
  }

  isMobile = () => {
    if (window.innerWidth < mobileMinWidth && !this.state.mobile) {
      this.setState({ mobile: true });
    } else if (window.innerWidth >= mobileMinWidth && this.state.mobile) {
      this.setState({ mobile: false });
    }
  };

  render() {
    return (
      <AppShell site={this.props.site} mobile={this.state.mobile}>
        <Switch>
            <Route path='/' exact={true} component={Home}/>;
            <Route path='/contact_us' exact={true} component = {ContactUs}/>
        </Switch>
      </AppShell>
    )
  }
}

const EnhancedApp = withRouter(App);
const AppRouter = props => (
  <Router>
    <EnhancedApp {...props} />
  </Router>
);
export default AppRouter;
