import React from 'react';
import PropTypes from 'prop-types';
// import { Link } from 'react-router-dom';
import {Container, Row, Col} from 'reactstrap';
import './contactInfo.scss';

class ContactInfo extends React.Component {
	render = () => {
		const {
			data1,
			data2,
		} = this.props;
		return (
			<div className="contactInfo">
				<Container>
				<Row>
					<Col xs={12} sm={6}>
						<p className="contactTitle">{window.paramsString.contact1Title}</p>
						{data1.map((item, key) => {
							console.log(key);
							return(
								<p key={key}>
									<span className="contactCategory">{item.category}:&nbsp;</span>
									<span className="contactDesc">{item.description}</span>
								</p>
							)
						})}
						<p className="contactTitle">{window.paramsString.contact2Title}</p>
						{data2.map((item, key) => {
							console.log(key);
							return(
								<p key={key}>
									<span className="contactCategory">{item.category}:&nbsp;</span>
									<span className="contactDesc">{item.description}</span>
								</p>
							)
						})}
					</Col>
					<Col xs={12} sm={6}>
						<p className="mapTitle">{window.paramsString.mapTitle}</p>
						<img className="contactMap" src={window.paramsString.mapImage} alt=""></img>
					</Col>
				</Row>
				</Container>
			</div>
		);
	};
}

ContactInfo.propTypes = {
	data1: PropTypes.array,
	data2: PropTypes.array,
};

ContactInfo.defaultProps = {
	data1: window.paramsString.contactInfo1,
	data2: window.paramsString.contactInfo2,
};

export default ContactInfo;
