import React from 'react';
import { Link } from 'react-router-dom';
import { isMobile } from '../../utils/utilities';
import './dropDownMenu.scss';

class DropdownMenu extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			showSideMenu : true,
		};
	  }

	getMenuItemTitle = (menuItem, index, depthLevel) => { return menuItem.title	};
	getMenuItemRouter = (menuItem, index, depthLevel) => { return menuItem.router};
	getMenuItem = (menuItem, depthLevel, index) => {
		let title = this.getMenuItemTitle(menuItem, index, depthLevel);
		let router = this.getMenuItemRouter(menuItem, index, depthLevel);
		if (menuItem.submenu && menuItem.submenu.length > 0 && menuItem.level === 0) {
			return (
				<li className="topMenu" key={index}>
					{title}
					<i className="fas fa-angle-down" />
					<DropdownMenu menu={menuItem.submenu} submenu={true} />
				</li>
			)
		} else if (menuItem.submenu && menuItem.submenu.length > 0 && menuItem.level !== 0) {
			return (
				<li className="subMenu" key={index}>
					{title}
					<i className="fas fa-angle-right" />
					<DropdownMenu menu={menuItem.submenu} submenu={true} />
				</li>
			);
		} else {
			return (
				<Link to={`/${router}`} key={index}>
					<li className="linkMenu">{title}</li>
				</Link>
			);
		}
	};

	componentDidMount() {
		this.isMobile();
		window.addEventListener('resize', this.isMobile);
	}

	isMobile = () => {
		isMobile() ? this.setState({ showSideMenu : false }) : this.setState({ showSideMenu : true  });
	}

	displayMenu = () => {
		this.state.showSideMenu ? this.setState({showSideMenu: false}) : this.setState({showSideMenu: true})
	}

	render = () => {
		let { menu } = this.props;
		let options = [];
		menu.map((item, index) => { return options.push(this.getMenuItem(item, 0, index)) });
		if (this.props.submenu && this.props.submenu === true) return <ul className="dropdownSubmenu">{options}</ul>;
		return <div className="tabBar" >
			<div id='logo' className='logo'>
				<img className="menuLogo" src={window.appConfig.logo} alt="" />
				<img className="menuFolder" src={window.appConfig.folder} alt="" onClick = {this.displayMenu}/>
			</div>
			<ul style={{ display: this.state.showSideMenu ? 'block' : 'none' }} className="dropdownMenu">{options}</ul>
		</div>;
	};
}



DropdownMenu.defaultProps = window.menuObj;
export default DropdownMenu;
